import React, {Component} from 'react';

export default class AddItem extends Component {
    render() {
        return(
            <div>
                <form className="add-item" onSubmit={this.props.addItem}>
                    <input type="text" placeholder="Enter Course name" onChange={this.props.getItem} />
                    <button>Add Course</button>
                </form>
            </div>
        )
    }
}