import React, { Component } from 'react';

export default class Item extends Component {
    state = {
        idEdit: false
    }

    toggleState = () => {
        let isEdit = this.state.isEdit;
        this.setState({
            isEdit: !isEdit
        })
    }

    updateItem = (e) => {
        e.preventDefault();
        this.props.editItem(this.props.index, this.input.value);
        this.toggleState();
    }

    renderForm1 = () => {
        return(
            <form onSubmit={this.updateItem}>
                <input type="text" defaultValue={this.props.name} className="updated-course-name" ref={v => {this.input = v}} />
                <button className="update" >Update</button>
            </form> 
        )
    }

    renderForm2 = () =>  {
        return(
            <form onSubmit={() => {this.props.deleteItem(this.props.id)}}>
                <span className="course-name">{this.props.name}</span>
                <span className="buttons">
                    <button className="edit" onClick={this.toggleState}>Edit course</button>
                    <button className="delete">Delete course</button>
                </span>
                </form>
        )
    }

    render() {
        let isEdit = this.state.isEdit;
        return (
            <div className="item">
                {isEdit ? this.renderForm1() : this.renderForm2()}
            </div>
        )
    }
}
