import React, {Component} from 'react';
import AddItem from './components/addItem';
import ItemsList from './components/items-list';
import './App.css';

export default class App extends Component {
  state = {
    items: [],
    item: ''
  }

  getItem = (e) => {
    this.setState({
      item: {
        id: Math.random(),
        name: e.target.value
      }
    })
  }

  addItem = (e) => {
    e.preventDefault();
    let items = [...this.state.items, this.state.item];
    this.setState({
      items,
      item: ''
    })

    e.target.elements[0].value="";
  }

  deleteItem = (id) => {
    let items = [...this.state.items],
      newItems = items.filter(item => id !== item.id ? item : null)

    this.setState({
      items: newItems
    })
  }

  editItem = (index, value) => {
    let items = this.state.items,
      item = items[index];
    item['name'] = value;

    this.setState({
      items
    })
  }

  render() {
    return (
      <div className="app">
        <div className="container">
          <h1 className="title">Add courses</h1>
          <AddItem getItem={this.getItem} addItem={this.addItem} />
          <ItemsList items={this.state.items} deleteItem={this.deleteItem} editItem={this.editItem} />
        </div>
      </div>
    )
  }
}
