import React, { Component } from 'react'
import Item from "./item"

export default class ItemsList extends Component {
    render() {
        return (
            <div className="items-list">
                {this.props.items.map((item, index) => <Item key={item.id} index={index} id={item.id} name={item.name} deleteItem={this.props.deleteItem} editItem={this.props.editItem} />)}
            </div>
        )
    }
}
